}

.cta-pup-up-svg {
  margin: auto;
  width: 2rem;
  height: 2rem;
  -webkit-transform: translateY(0.125rem);
          transform: translateY(0.125rem);
}
.cta-pup-up-svg > * {
  fill: var(--White);
}

.cta-container:hover {
  cursor: pointer;
}

.cta-container-white {
  border: 0.3rem solid var(--White);
  color: var(--White);
  box-shadow: none;
}

.cta-container-base {
  border: 0.3rem solid var(--Secondary);
  background-color: var(--Secondary);
  color: var(--White);
}

.cta-container-blue {
