.card-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  grid-gap: 2.5rem;
  gap: 2.5rem;
}

.card-box {
  width: 50rem;
  height: 25rem;
  border-radius: 1.25rem;
  overflow: hidden;
  display: flex;
  box-shadow: 0 0 1rem -0.3rem var(--BlackShadow);
}

.card-info {
  width: 50%;
  height: 100%;
  background-color: var(--White);
  padding: 2.5rem;
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  align-items: flex-start;
}

.card-view-container {
  margin-top: auto;
  padding: 0.5rem 1.5rem;
  border-radius: 5rem;
  border: 0.3rem solid var(--Secondary);
  text-decoration: none;
  transition: all 200ms ease-out;
}

.card-view {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.card-img {
  width: 50%;
  height: 100%;
  background-color: var(--ImgBackgorund);
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-img > img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.card-date {
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transform: rotate(-90deg) translate(-50%);
          transform: rotate(-90deg) translate(-50%);
  width: 25rem;
  margin-right: -24rem;
}

.card-arrow {
  width: 1.125rem;
  fill: var(--Secondary);
  transition: all 200ms ease-out;
}

.card-view-container:hover .card-arrow {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

.card-view-container:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.card-container:nth-child(even),
.card-container:nth-child(even) > .card-box {
  flex-direction: row-reverse;
}

.card-container:nth-child(even) > .card-date {
  margin-right: 0;
  margin-left: -24rem;
  -webkit-transform-origin: right;
          transform-origin: right;
  -webkit-transform: rotate(-90deg) translate(50%);
          transform: rotate(-90deg) translate(50%);
}

.bubble-container {
  background-color: rgba(255, 255, 255, 0);
  position: relative;
  border-radius: 50%;
  border: 0.5rem solid var(--Secondary);
  overflow: hidden;
  transition: background 150ms ease-in;
  transition-delay: 150ms;
  box-shadow: 0 0 1rem 0rem var(--BlackShadow);
  text-decoration: none;
  display: flex;
  flex-direction: column;
  grid-gap: 5%;
  gap: 5%;
  justify-content: center;
  align-items: center;
}

.bubble-img-container {
  z-index: -1;
  position: absolute;
  background-color: transparent;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  -webkit-mask-image: -webkit-radial-gradient(white, black);
}

.bubble-img {
  position: absolute;
  z-index: -1;
  object-fit: contain;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
