}

.bubble-title {
  text-align: center;
  width: 80%;
  font-size: 3.125rem;
  font-weight: 600;
  line-height: 4.375rem;
  opacity: 0;
  color: white;
  transition: all 200ms ease-out;
  -webkit-transform: translateY(-2rem);
          transform: translateY(-2rem);
  overflow-wrap: break-word;
}

.bubble-git {
  width: 5rem;
  opacity: 0;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  transition: opacity 200ms ease-out, -webkit-transform 300ms ease-in;
  transition: opacity 200ms ease-out, transform 300ms ease-in;
  transition: opacity 200ms ease-out, transform 300ms ease-in, -webkit-transform 300ms ease-in;
}

.bubble-container:hover > .bubble-title {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.bubble-container:hover > .bubble-git {
  opacity: 1;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: all 300ms ease-out;
  transition-delay: 175ms;
}

.bubble-container:hover {
  background-color: var(--Black60);
  transition: background 100ms;
}

